





import {Component, Prop, Watch, Vue} from 'vue-property-decorator';
import {ShH, ShCoordinates, ShRect, ShPlatform, ShMap} from './ShCollectionPointsMap.interface';
import _ from 'lodash';
import axios from 'axios';

declare global {
  export interface Window {
    H: any;
  }
}
const H: ShH = window.H;

@Component
export default class ShCollectionPointsMap extends Vue {
  @Prop() query!: string;
  @Prop() selectedListMarker!: any;
  @Prop() width!: string;
  @Prop() height!: string;
  @Prop() eventBus!: any;
  @Prop({default: 0}) cashOnDelivery!: number;
  @Prop({default: '/ajax'}) apiUrl!: string;
  @Prop({default: 'shop.checkout.points'}) apiMethod!: string;
  @Prop({default: () => ['Poczta', 'Automat pocztowy', 'Orlen', 'Ruch', 'Zabka', 'Paczkomat', 'Fresh', 'Dhl parcel', 'Paczka w ruchu']}) pointType!: string[];

  public created () {
    if (this.eventBus) {
      this.eventBus.$on('shipping-changed', () => {console.log('fired');this.getCollectionPoints()})
    }
  }

  public beforeDestroy () {
    if (this.eventBus) {
      this.eventBus.$off('shipping-changed', () => {console.log('destroy');this.getCollectionPoints()})
    }
  }

  public mapDimensions: any = {
    width: this.width,
    height: this.height
  };
  public markersData: any[] = null;
  public selectedMarker: any = null;
  public map: ShMap = null;
  public mapEvents: any = null;
  public platform: any = null;
  public maptypes: ShPlatform = null;
  public geocoder: ShPlatform = null;
  public hereAuth: any = {
    app_id: "tfgHE1bvtuF8dFM7Czfh",
    app_code: "E5X9edxHwCKKFU6HQOjobQ",
    useCIT: true,
    useHTTPS: true,
  };
  public activeMarkers: any = null;
  public isFetching: boolean = false;
  public cancel: any;
  public CancelToken = axios.CancelToken;

  public locateAddress(result: any) {
    if (result.Response && result.Response.View.length) {
      const locations = result.Response.View[0].Result;
      // currentCenter
      const position = {
        lat: locations[0].Location.DisplayPosition.Latitude,
        lng: locations[0].Location.DisplayPosition.Longitude
      };

      this.setNewMapCenter(position, 14);
    }
  }

  public setNewMapCenter(position: any, zoom: number) {
    this.map.setCenter(position);
    this.map.setZoom(zoom);
  }

  public getCollectionPoints() {
    const CancelToken = axios.CancelToken;
    let cancel: any;
    const self = this;
    const rect: ShRect = this.map.getViewBounds();
    const mapCenter: ShCoordinates = rect.getCenter();
    const malTopLeft: ShCoordinates = rect.getTopLeft();
    const mapBottomRight: ShCoordinates = rect.getBottomRight();
    const position: ShCoordinates = {
      lat: mapCenter.lat,
      lng: mapCenter.lng,
    };
    let points = [];
    const data = {
      method: this.apiMethod,
      lat0: mapCenter.lat,
      long0: mapCenter.lng,
      lat1: malTopLeft.lat,
      long1: malTopLeft.lng,
      lat2: mapBottomRight.lat,
      long2: mapBottomRight.lng,
      type: this.pointType,
      cash_on_delivery: this.cashOnDelivery,
    };

    if (this.cancel) {
      this.cancel();
    }

    axios.get(this.apiUrl, {
      params: data,
      cancelToken: new CancelToken(function executor(c) {
        self.cancel = c;
      })
    }).then(response => {
      if (response && response.data.points) {
        this.markersData = response.data.points;
        this.drawMarkers(this.markersData);
        this.$emit('loadedMarkers', this.markersData );
      }

    }).catch((thrown) => {
      if (axios.isCancel(thrown)) {
        console.log("Request canceled", thrown);
      } else {
        // handle error
      }
    });
  }

  public drawMarkers(points: any) {
    let i;
    let markers = [];
    let markerIconHtml = "";

    for (i = 0; i < points.length; i++) {
      let position = {
        lat: points[i].latitude,
        lng: points[i].longitude
      };

      if (points[i].point_type === "AUTOMAT POCZTOWY") {
        markerIconHtml = "<div class=\"map-marker marker-pocztomat\" data-lat=\"" + points[i].latitude + "\" data-lng=\"" + points[i].longitude + "\" data-marker-id=\"" + points[i].external_id + "\"></div>";
      } else if (points[i].point_type === "POCZTA") {
        markerIconHtml = "<div class=\"map-marker marker-poczta\" data-lat=\"" + points[i].latitude + "\" data-lng=\"" + points[i].longitude + "\" data-marker-id=\"" + points[i].external_id + "\"></div>";
      } else if (points[i].point_type === "ORLEN") {
        markerIconHtml = "<div class=\"map-marker marker-orlen\" data-lat=\"" + points[i].latitude + "\" data-lng=\"" + points[i].longitude + "\" data-marker-id=\"" + points[i].external_id + "\"></div>";
      } else if (points[i].point_type === "RUCH") {
        markerIconHtml = "<div class=\"map-marker marker-ruch\" data-lat=\"" + points[i].latitude + "\" data-lng=\"" + points[i].longitude + "\" data-marker-id=\"" + points[i].external_id + "\"></div>";
      } else if (points[i].point_type === "DHL PARCEL") {
        markerIconHtml = "<div class=\"map-marker marker-dhl\" data-lat=\"" + points[i].latitude + "\" data-lng=\"" + points[i].longitude + "\" data-marker-id=\"" + points[i].external_id + "\"></div>";
      } else if (points[i].point_type === "ZABKA") {
        markerIconHtml = "<div class=\"map-marker marker-zabka\" data-lat=\"" + points[i].latitude + "\" data-lng=\"" + points[i].longitude + "\" data-marker-id=\"" + points[i].external_id + "\"></div>";
      } else if (points[i].point_type === "FRESH") {
        markerIconHtml = "<div class=\"map-marker marker-freshmarket\" data-lat=\"" + points[i].latitude + "\" data-lng=\"" + points[i].longitude + "\" data-marker-id=\"" + points[i].external_id + "\"></div>";
      } else if (points[i].point_type === "PACZKOMAT") {
        if (points[i].external_id.indexOf("ZABKA") !== -1) {
          markerIconHtml = "<div class=\"map-marker marker-paczkomat-zabka\" data-lat=\"" + points[i].latitude + "\" data-lng=\"" + points[i].longitude + "\" data-marker-id=\"" + points[i].external_id + "\"></div>";
        } else if (points[i].external_id.indexOf("FRESH") !== -1) {
          markerIconHtml = "<div class=\"map-marker marker-paczkomat-freshmarket\" data-lat=\"" + points[i].latitude + "\" data-lng=\"" + points[i].longitude + "\" data-marker-id=\"" + points[i].external_id + "\"></div>";
        } else {
          markerIconHtml = "<div class=\"map-marker marker-paczkomat\" data-lat=\"" + points[i].latitude + "\" data-lng=\"" + points[i].longitude + "\" data-marker-id=\"" + points[i].external_id + "\"></div>";
        }
      } else if (points[i].point_type === "PACZKA W RUCHU") {
        markerIconHtml = "<div class=\"map-marker marker-paczka-w-ruchu\" data-lat=\"" + points[i].latitude + "\" data-lng=\"" + points[i].longitude + "\" data-marker-id=\"" + points[i].external_id + "\"></div>";
      }
      //
      let markerIcon = new H.map.DomIcon(markerIconHtml, {
        // the function is called every time marker enters the viewport
        onAttach: (clonedElement: any, domIcon: any, domMarker: any) => {
          clonedElement.addEventListener("touchstart", this.markerAction);
          clonedElement.addEventListener("click", this.markerAction);
        },
        // the function is called every time marker leaves the viewport
        onDetach: (clonedElement: any, domIcon: any, domMarker: any) => {
          clonedElement.removeEventListener("touchstart", this.markerAction);
          clonedElement.removeEventListener("click", this.markerAction);
        }
      });
      let marker = new H.map.DomMarker(position, {icon: markerIcon});
      markers.push(marker);
    }

    if (this.activeMarkers) {
      this.map.removeObjects(this.activeMarkers);
    }

    this.activeMarkers = markers;

    this.map.addObjects(markers);
    setTimeout(() => {
      this.restoreSelectedMarker();
    }, 50);

  }

  public restoreSelectedMarker () {

    if (this.selectedMarker) {
      this.markSelectedMarker(this.selectedMarker)
    }

    if (this.selectedListMarker) {
      this.markSelectedMarker(this.selectedListMarker)
    }
  }

  public markerAction(e: any) {
    this.clearActiveMarkers();
    e.target.classList.add("active");
    this.selectedMarker = _.find(this.markersData, {'external_id': e.target.dataset.markerId});
    this.$emit('selectMarker', this.selectedMarker);
  }

  public markSelectedMarker (marker:any) {
    let mapMarker;
    this.clearActiveMarkers();
    if (marker) {
      mapMarker = document.querySelectorAll("[data-marker-id='" + marker.external_id + "']");

      if (mapMarker.length) {
        mapMarker[0].classList.add("active");
      }
    }
  }

  public clearActiveMarkers () {
    let elems = document.querySelectorAll(".map-marker");
    if (elems.length > 0) {
      _.each(elems, (el: any) => {
        el.classList.remove("active");
      });
    }
  }

  public mounted() {
    this.platform = new (H.service.Platform as any)(this.hereAuth);
    this.maptypes = this.platform.createDefaultLayers();
    this.geocoder = this.platform.getGeocodingService();
    // Instantiate (and display) a map object:
    this.map = new H.Map(
      this.$el,
      this.maptypes.normal.map,
      {
        zoom: 10,
        center: {lng: 13.4, lat: 52.51},
      },
    );
    this.maptypes.normal.map.setMin(13);
    this.maptypes.normal.map.setMax(19);
    const mapEvents = new H.mapevents.MapEvents(this.map);
    const ui = H.ui.UI.createDefault(this.map, this.maptypes);
    // drag
    new H.mapevents.Behavior(mapEvents);
    // remove settings from ui
    ui.removeControl("mapsettings");

    this.map.addEventListener("mapviewchangeend", () => {
      this.processMapState();
    });

    if (this.query) {
      this.geocoder.geocode({searchText: this.query}, this.locateAddress, function (e: any) {
      });
    }

    // this.getCollectionPoints();
  }

  public processMapState() {
    this.getCollectionPoints();
    this.$emit('mapChanged', true)
  }

  @Watch("query")
  private watchQuery(newVal: any, oldVal: any) {
    this.geocoder.geocode({searchText: this.query}, this.locateAddress, function (e: any) {
    });
  }

  @Watch("cashOnDelivery")
  private watchCashOnDelivery(newVal: any, oldVal: any) {
    this.getCollectionPoints();
  }

  @Watch("pointType")
  private watchPointType(newVal: any, oldVal: any) {
    this.getCollectionPoints();
  }

  @Watch("selectedListMarker")
  private watchSelectedListMarker(newVal: any, oldVal: any) {
    this.markSelectedMarker(newVal);
  }
}
